import { Injectable } from '@angular/core';
import { Project } from '../models/project.model';
import { BehaviorSubject, Observable } from 'rxjs/Rx';
import { Http, Response, Headers } from '@angular/http';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class DataService {

  constructor(
    private http: Http
  ) {}

  getProject(id: string): Promise<Project> {
    return this.http.get(`api/v1/project/${id}`)
    .toPromise()
    .then((res: Response) => {
      console.log(res);
      return res.json();
    })
    .catch(console.error)
  }

  addProject(newProject: Project) {
    newProject.contributors= [newProject.host];
    const headers = new Headers({
      'content-type': 'application/json'
    });
    return this.http.post('api/v1/project', newProject, headers)
    .toPromise()
    .then((res: Response) => {
      res.json();
    })
    .catch(console.error)
  }

  save(id:string, category: string ,content: string) {
    const headers = new Headers({
      'content-type': 'application/json'
    });
    var data = {
      'category': category,
      'content' : content
    }
    return this.http.put(`api/v1/project/${id}`, data, headers)
    .toPromise()
    .then((res: Response) => {
      console.log('info updated');
      return res.json();
    })
    .catch(console.error);
  }
}



