import { Component, OnInit, Inject } from '@angular/core';
import { Project } from '../../models/project.model';
import { ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-project-detail',
  templateUrl: './project-detail.component.html',
  styleUrls: ['./project-detail.component.css']
})
export class ProjectDetailComponent implements OnInit {

  project: Project;
  editorEnabled: Boolean;
  newNote: String;
  desc: String;
  history: String[];

  constructor(
    @Inject('data') private data,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.disableEditor();
    this.newNote = '';
    this.route.params.subscribe(params => {
      this.project = this.data.getProject(params['id'])
        .then((project) => {
          this.project = project;
          this.desc = project.desc;
          this.history = project.history;
        })
        .catch(error => console.error);
    });
  }

  enableEditor(): void {
    this.editorEnabled = true;
  }

  disableEditor(): void {
    this.editorEnabled = false;
  }

  saveDesc(): void {
    this.data.save(this.project.id, 'description', this.desc)
      .then(newInfo => this.desc = newInfo)
      .catch(error => console.log(error));
  }

  addNote(): void {
    this.data.save(this.project.id, 'note', this.newNote)
      .then(newInfo => this.history = newInfo)
      .catch(error => console.log(error));
    this.newNote = '';
  }


}



